import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import './App.css';
import Navbar from './components/Navbar';
import Getfile from './components/Getfile';
import Showfile from './components/Showfile';



const App = () => {

   const toggleDisplay = (e) => {
      console.log(e);
      alert('Clicked');
   }

  return (
    <>
      <div className='container'>
         <div className='nav'>
            <Navbar  />
         </div>
         <div className='main'>
            <Router>
               <Switch>
                  <Route exact Path='/' component={Getfile} toggle={toggleDisplay}/>
                  <Route Path='/show' component={Showfile} />
               </Switch>
            </Router>
         </div>
      </div>
    </>
  );
}

export default App;
