import { useState } from 'react';
import Readsub from './Readsub';

const Readme = (props) => {

   const buildInfo = (data,cb) => {
      const completed = {};
      const array = [];
      const subData = [];
      const sub = [];
      let count = 0;
      for (const keyInfo in data) {
         const main = data[keyInfo];
         const type = typeof(main);
         let item = null;
         if(type === 'string' || type === 'number' || type === 'boolean'){
            if(keyInfo !== 'count'){
               count++
               item = <div className='file-info' key={keyInfo +'-'+count+'-'+ data['count']}>
                        <span className='info-key'>{keyInfo}:</span>
                        <span className='info-data'>{main}</span>
                     </div>;
                     
               array.push(item)
            }
         }
         if(type === 'object'){
            item = {
               title: keyInfo,
               data: main
            };
            // console.log(item.title,':\n\t',item.data);
            subData.push(item)
         }
      };
      if(subData.length > 0){
         const newPayload = {
            count: data.count,
            subData: subData
         }

         sub.push(<div className='sub-data' key={'subkey-'+newPayload.subData.title+newPayload.count}><Readsub data={newPayload} toggle={props.toggle}/></div>);
      }
      if(array.length > 0){
         completed.main = array;
      }
      if(sub.length > 0){
         completed.sub = sub;
      }
      return completed;
   }

   const file = buildInfo(props.file);

   if(file.main && file.sub){
      return (
         <>
            <div className='unnested'>
               {file.main}
            </div>
            <div className='nested'>
               {file.sub}
            </div>
         </>
      )
   } else if (file.main && !file.sub){
      <div className='unnested'>
         {file.main}
      </div>
   }
}

export default Readme;
