import { useState } from 'react';
import Pages from './Pages';
import Display from './Display';

const Processor = (props) => {

   const holding = [];
   const [isHolding,setIsHolding] = useState(()=> false);
   const [place,setPlace] = useState(()=> 0);
   const [showAmount,setShowAmount] = useState(()=> 250);

   const divide = (filedata) => {
      let temp = [];
      let count = 0;
      for(const data of filedata){
         temp.push(data);
         if(temp.length === showAmount){
            holding[count] = temp;
            count++;
            temp = [];
         }
      }
      if(temp.length > 0 && temp.length < showAmount){
         holding[count] = temp
         count++;
         temp = [];
      }
   }

   const selectPage = (e) => {
      const classV = e.target.classList.value
      if(classV === 'page-num'){
         setPlace(Number(e.target.innerText) - 1);
      }
   }

   divide(props.data);
   if(holding.length > 0){
      return(
         <>
            <div className='display'>
               <div className='pages' onClick={ selectPage }>
                  <Pages page={holding.length}/>
               </div>
               <Display data={holding[place]} toggle={props.toggle}/>
            </div>
         </>
      )
   } else {
      return(
         <>
            <div className='noting-display'>
               <h1>Nothing here yet!</h1>
            </div>
         </>
      )
   }
}

export default Processor;
