const Pages = (props) => {
   const letsCount = (num) => {
      const array = [];
      for(let i = 1; i <= num; i++){
         array.push(<span className='page-num' key={i}>{i}</span>)
      }
      return array;
   }

   return(
      <>
         <div className='label'>
            <h3>Number of Pages:</h3>
         </div>
         <div className='nums'>
            {letsCount(props.page)}
         </div>
      </>
   )

};

export default Pages;
