import Readme from './Readme';

const Display = ({data,toggle}) => {

   // console.log(props.data[0]);

   const buildDisplay = (data) => {
      const array = [];
      let count = 0;
      for(const log of data){
         count++
         log.count = count;
         array.push(<div className='file-row' key={count}><Readme file={log} toggle={toggle}/></div>);
      }
      return array;
   };


   return(
      <>
         <h1>Your file's Data:</h1>
         <div className='file-data'>
            {buildDisplay(data)}
         </div>
      </>
   )
}

export default Display;
