const Readsub = (props) => {
   const hideMe = (elm,id) => {
     const closeElm = document.querySelectorAll(elm);
     for(const eachElm of closeElm){
         if(eachElm.id !== id && eachElm.classList.value.indexOf('hidden') > -1 == false){
            eachElm.classList.add('hidden');
        }
     } 
   }

   const toggleDisplay = (e) => {
      const array = e.target.id.split('-');
      const id = array[0]+'-'+array[array.length-1]
      const list = e.target.classList.value;
      if(list.indexOf('menu') > -1 === true){
         hideMe('.nested-data',id);

      }
      const button = document.getElementById(e.target.id)
      const openData = document.getElementById(id);
      openData.classList.toggle('hidden');
      button.classList.toggle('selected')
   }

   const buildMenu = (file) => {
      const array = [];
      for(const list of file.subData){
         array.push(<li onClick={toggleDisplay} id={list.title+'-menu-'+file.count} className='menu' key={list.title+file.count}>{list.title}</li>);
      }
      return array;
   }

   const buildSubs = (file) => {
      const array = [];
      let subCount = 0;
      let subSubCount = 0;
      const moreNested = (count,title,payload) => {
         const nestedArray = [];
         const subData = [];

         let subTitleEnd = 'nested';
         for(let i = 0; i < subCount; i++){
            subTitleEnd += '-sub';
         }
         subCount++
         for(const nestedPartal in payload){
            const nestedType = typeof(payload[nestedPartal]);
            if(nestedType === 'string' || nestedType === 'number' || nestedType === 'boolean' ){
               if(nestedPartal !== 'title' && nestedPartal !== 'data'){
                  const classId = title+subTitleEnd+'-no'+count;
                  const key = title+'-'+nestedPartal+subTitleEnd+'-no'+count;
                  nestedArray.push(
                     <div className='data-sub-key' key={key}><section><span>{nestedPartal}:</span><span>{"'"+payload[nestedPartal]+"'"}</span></section></div>
                  )
               }
            }
            if(nestedType === 'object' && nestedPartal !== 'data'){
               subSubCount++
               const newCount = count+subSubCount;
               subData.push(<div className='nested-sub'><section id={nestedPartal+'-menu-' + count} className={subTitleEnd+'-menu'}>{nestedPartal}:</section><section id={nestedPartal + '-' + count} className={subTitleEnd+'-data hidden'}>{moreNested(newCount,nestedPartal,payload[nestedPartal])}</section></div>);
            }
         }
         for(const subs of subData){
            nestedArray.push(subs)
         }
         return nestedArray;
      }

      for(const data of file.subData){

         const breakDown = (payload) => {
            let breakCount = 0;
            for(const partal in payload){
               if(payload[partal] == null || payload[partal] == ''){
                  payload[partal] = 'null/blank';
               }
               const type = typeof(payload[partal]);
               if(type === 'string' || type === 'number' || type === 'boolean' ){
                  if(partal !== 'title'){
                     array.push(
                        <div id={data.title+'-'+file.count} className='data-sub' key={data.title+file.count+partal}><span className='sub-key'>{partal}:</span><span>{payload[partal]}</span></div>
                     )
                  }
               }
               if(type === 'object'){
                  breakCount++
                  const newStart = file.count + breakCount;
                  const cleanData = <div id={data.title+'-'+file.count} className='nested-data'>{moreNested(newStart,partal,payload[partal])}</div>;
                  array.push(cleanData);
               }
            }
         }
         breakDown(data);
      }
      return array;
   }

   return(
      <>
         <div>
            <ul>
               {buildMenu(props.data)}
            </ul>
         </div>
         <div className='subDisplay'>
            {buildSubs(props.data)}
         </div>

      </>
   )
};

export default Readsub;
