import { useState } from 'react';
import Processor from './Processor';

const Getfile = ({toggle}) => {
   const [theFile, theFileState] = useState(()=>[]);

   const verifyData = (payload,cb) => {
      const data = typeof(payload) == 'string' && payload.length > 0 ? payload : false;
      if(data){
         cb(false,data);
      } else {
         cb(true);
      }
   }

   const convertData = (payload,cb) => {
      const convertedPayload = [];
      const data = typeof(payload) == 'string' && payload.length > 0 ? payload : false;
      if(data){
         const newPayload = data.split('\n');
         if(newPayload.length > 0){
            for(const index in newPayload){
               const payloadInt = newPayload[index].substring(0,2);
               const jsonFormat = '{"';
               if(payloadInt === jsonFormat){
                  const convertedJson = JSON.parse(newPayload[index]);
                  convertedPayload.push(convertedJson);
                  theFile.push(convertedJson);
               }
            }
            cb(false,convertedPayload);
         }
      } else {
         cb(true);
      }
   }


   const grabFile = (e) => {
      if(e.target.files.length > 0){
         const file = e.target.files[0];
         printOut(file);
      }
   };

   const printOut = (data) => {
      const reader = new FileReader();
      reader.onload = function(evt) {
         verifyData(evt.target.result,(err,data) => {
            if(!err && data){
               updateFileData(data);
            }
         });
      };
      reader.readAsText(data);
   };

   const updateFileData = (data) => {
      convertData(data,(err,fileData) => {
         if(!err && fileData){
            theFileState(theFile=>[...theFile,fileData]);
         }
      });
   }
   if(theFile.length <= 0){
      return (
         <>
            <div className='collect-file'>
               <input type='file' onChange={grabFile} />
            </div>
         </>
      )
   } else {
      return (
         <>
            <div className='form-data'>
               <input type='file' onChange={grabFile} />
            </div>
            <div className='file-data'>
               <Processor data={theFile} toggle={toggle}/>
            </div>
         </>
      )
   }
}

export default Getfile;
